:root {
  --main-green: #0ae011;
  --main-white: #ffffff;
  --main-black: #000000;
  --btn-green: #4caf50;
  --section-green: #00b14f;
  --main-gray: #383e43;
  --main-yellow: #d6ef52;
  --footer-gray: #363a45;
  --very-green: #00ba51;
  --gray-bg: #f5f5f5;
}
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  height: 100vh;
  width: 100vw;
  overflow-x: hidden;
  scroll-behavior: smooth;
}
body {
  line-height: 1.4em;
  font-family: "lato", sans-serif;
  overflow-x: hidden;
  padding-top: 4.375rem;
}
a {
  text-decoration: none;
}
ul {
  list-style: none;
}
.flex-row {
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  text-align: center;
}

/* navbar */
.logo {
  font-family: "Monoton", cursive;
  color: var(--main-green) !important;
  font-size: 1.9rem !important;
  font-weight: 350;
}
#navbar {
  background-color: #ffffff !important;
  -webkit-box-shadow: 0px 1.5px 1px -1px rgba(194, 184, 184, 0.75);
  -moz-box-shadow: 0px 1.5px 1px -1px rgba(194, 184, 184, 0.75);
  box-shadow: 0px 1.5px 1px -1px rgba(194, 184, 184, 0.75);
  color: #000000 !important;
}

.custom-navbar-link {
  color: #000000 !important;
  font-size: 1.25rem;
  transition: all ease-in-out 0.4s;
  font-family: "Cuprum", sans-serif;
}
.custom-navbar-dropdown-link {
  color: #000000 !important;
  font-size: 1.25rem;
  transition: all ease-in-out 0.4s;
  font-family: "Cuprum", sans-serif;
}

.custom-navbar-link:hover,
.custom-navbar-link:focus {
  color: #888888 !important;
}

/* showcase section */

#showcase-section {
  background-color: var(--section-green);
  /* width: 100%; */
}
.showcase-big-heading {
  font-size: 3.75rem;
  color: var(--main-white);
}

.food-delivery-p-logo {
  color: var(--main-white);
  font-size: 1.3rem;
  font-weight: bold;
  font-family: "Russo One", sans-serif;
}
.showcase-container-link {
  color: var(--main-white);
  transition: all ease 0.5s;
}
.showcase-container-link:hover,
.showcase-container-link:focus {
  color: var(--main-white);
  text-decoration: none;
  border-bottom: 1px solid var(--main-white);
}
/* services section */
.section-services {
  background-color: #f5f5f5;
}
.services-big-heading {
  font-size: 3.75rem;
  color: var(--main-black);
}

.service-section-get-app-btn {
  border: none;
  outline: none;
  transition: ease all 0.3s;
  margin: 0.5rem;
  /* width: 10rem; */
}
.service-section-get-app-btn:hover,
.service-section-get-app-btn:focus {
  transform: scale(1.1);
}
.services-downloadApp-heading {
  font-size: 2em;
  font-weight: bold;
  color: var(--main-gray);
}

/* newsletter */
#newsletter {
  background-color: var(--section-green);
  min-height: 19.625rem;
  width: 100%;
}
.newsletter-heading {
  color: var(--main-yellow);
  font-size: 2.6em;
  line-height: 1.2em;
}

.newsletter-link {
  color: var(--main-white);
  transition: all ease 0.5s;
}
.newsletter-link:hover,
.newsletter-link:focus {
  color: var(--main-white);
  text-decoration: none;
  border-bottom: 1px solid var(--main-white);
}
#newsletter-child-container {
  padding: 6.25rem 7.5rem;
}

/* prefooter section */
.prefooter-wrapper {
  width: 100%;
  height: auto;
  overflow: hidden;
  background-color: #eaeff2;
}
.prefooter-btn {
  border: none;
  outline: none;
  background: none;
  margin: 0 0.5rem;
  font-size: 2rem;
  transition: all ease 0.3s;
}
.prefooter-btn:hover,
.prefooter-btn:focus {
  transform: scale(1.1);
  color: var(--btn-green);
}

#prefooter-second-container h4 {
  font-size: 1rem;
  font-weight: bold;
}
#prefooter-second-container .navbar-brand {
  font-size: 1rem;
  font-weight: bold;
}
#prefooter-second-container a {
  font-size: 0.875rem;
  color: var(--main-black);
  transition: all ease 0.3s;
}

#prefooter-second-container a:focus,
#prefooter-second-container a:hover {
  color: #889999;
}
#prefooter-link-logo {
  font-family: "Monoton", cursive;
  color: var(--main-green) !important;
  font-size: 1.4rem !important;
  font-weight: 350;
}

/* footer section */
.main-footer {
  background-color: var(--footer-gray);
  /* width: 100%; */
  margin: auto;
  padding: 1rem 2rem;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.footer-link {
  color: var(--main-white) !important;
  margin: 0 0.5rem;
  font-size: 1em;
}

.footer-p-text {
  color: var(--main-white);
  font-size: 1.143em;
}
.footer-getapp-btn {
  max-height: 2.375rem;
  max-width: 6.25rem;
  border: none;
  outline: none;
  background: none;
  transition: ease 0.3s all;
}

.footer-getapp-btn:hover {
  transform: scale(1.1);
}

/* partner driver page*/

#partner-driver-wrapper h4 {
  font-size: 1.6em;
  margin-bottom: 0.9rem;
}
.partner-driver-heading {
  color: var(--very-green);
  font-weight: 600;
  font-size: 2.571rem;
  text-align: center;

  margin-top: 23px;
  margin-bottom: 23px;
}

.partner-driver-p {
  font-size: 1.143em;
}
.heading-green {
  color: var(--very-green);
}
.partner-driver-2nd-heading {
  font-size: 2.571rem;
}

.become-driverpartner-link {
  color: #1aa6f6 !important;
  transition: 0.3s ease all;
  font-size: 1.23rem;
  font-weight: 350;
}

.circle-count {
  width: 1.875rem;
  height: 1.875rem;
  border: 1.5px solid var(--main-green);
  border-radius: 50%;
  color: var(--main-green);
}

/* section-downloadApp */
.section-downloadApp {
  background-image: url("./assets/img/photos/dg-grab-back.jpg");
  min-height: 22.5rem;
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
}
.downloadApp-wrapper {
  display: flex;
  align-items: center;
  justify-content: center;
}
.dowloadApp-btn-wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.dowloadApp-iosBtn,
.downloadApp-androidBtn {
  width: 80%;
  border: none;
  outline: none;
  background: none;
  transition: all ease 0.3s;
}

.dowloadApp-iosBtn:hover,
.downloadApp-androidBtn:hover {
  transform: scale(1.02);
}

/* driver-register */
.driver-register-wrapper {
  min-height: 31rem;
  background-color: var(--gray-bg);
}

.driver-register-wrapper h1 {
  font-weight: 600;
  font-size: 3.1rem;
  margin-bottom: 10px;
  color: var(--main-green);
  line-height: 1;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
}

.driver-register-wrapper h2 {
  font-weight: 600;
  font-size: 2.1rem;
  margin-bottom: 10px;
  color: var(--main-green);
  line-height: 1;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.6);
}

#why-wait-container {
  background-image: url("./assets//img//photos/Unknown-8.jpeg");
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center center;
  height: 308px;
}

.become-driver-reg-btn-up {
  background-color: var(--main-green);
  color: var(--main-white);
  padding: 0.8rem;
  font-size: 1.2rem;
  border-radius: 0.25rem;
}
.become-driver-reg-btn-up:hover {
  text-decoration: none;
  color: var(--main-white);
}

@media only screen and (max-width: 767px) {
  .prefooter-wrapper {
    width: 95%;
    max-width: 95%;
  }
}

@media screen and (max-width: 550px) {
  .showcase-big-heading {
    font-size: 1.5rem;
  }
  .services-big-heading {
    font-size: 1.5rem;
  }
  #order-throught-website-container {
    flex-direction: column;
    align-items: center;
  }
  #showcase-order-through-app-container {
    flex-direction: column;
    align-items: center;
  }
  .services-downloadApp-heading {
    font-size: 1em;
  }
  .service-section-get-app-btn {
    width: 5rem;
  }
  #services-getappbtn-container {
    align-items: center;
  }
  #newsletter {
    min-height: 10.625rem;
  }
  #newsletter-child-container {
    padding: 0.5rem;
  }

  .newsletter-heading {
    font-size: 1.8em;
    line-height: 1em;
  }
  .newsletter-link-container {
    width: 60%;
  }
  #prefooter-second-container {
    flex-direction: column;
    justify-content: center;
    align-items: center;
    /* padding: 25px 50px; */
  }
  .main-footer {
    flex-direction: column;
  }
  .downloadApp-wrapper {
    flex-direction: column;
  }
  .become-driver-reg-btn-up {
    margin-top: 2rem;
  }

  #driver-register-form-wrapper {
    flex-direction: column;
    align-items: stretch;
    justify-content: space-evenly;
  }
  #becomedriver-heading-wrapper {
    width: 90%;
    display: flex;
    justify-content: center;
    align-self: center;
  }
  #becomedriver-heading-wrapper h1 {
    letter-spacing: 1.5px;
  }
  .driver-register-wrapper h1 {
    font-size: 1.55rem;
  }
  #partner-driver-heading-whyjoin-wrapper {
    padding: 1.25rem;
    width: 95%;
  }
  #why-wait-container-inside-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: space-evenly;
  }

  #whybemerchant-wrapper {
    flex-direction: column;
    align-items: center;
    justify-content: center;
  }
  .heading-green {
    font-size: 1.2rem;
  }
  .services-h2 {
    font-size: 0.8rem;
  }
}
